.header {
  width: 100%;
  display: flex;
  padding: 32px;
  justify-content: space-between;
  align-items: center;

  &__title {
    color: #424242;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    user-select: none;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    cursor: pointer;

    span {
      color: #41522e;
      text-align: right;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      letter-spacing: -0.408px;
    }
    svg {
      width: 24px;
      height: 24px;
    }
  }
}
